import React from 'react'
import { Form } from 'antd'
import SearchBox from '../index'

// 
const dataSource = [
  {
    value: 'Zhejiang',
    name: '',
    children: [
      {
        value: 'Hangzhou',
        name: '',
        children: [
          {
            value: 'Yuhang',
            name: ''
          },
          {
            value: 'Gongshu',
            name: ''
          }
        ]
      },
      {
        value: 'Ningbo',
        name: '',
        children: [
          {
            value: 'Ninghai',
            name: ''
          },
          {
            value: 'Xiangshan',
            name: ''
          }
        ]
      }
    ]
  },
  {
    value: 'Anhui',
    name: '',
    children: [
      {
        value: 'Wuhu',
        name: '',
        children: [
          {
            value: 'Wuwei',
            name: ''
          }
        ]
      },
      {
        value: 'Maanshan',
        name: '',
        children: [
          {
            value: 'Hexian',
            name: ''
          }
        ]
      }
    ]
  }
]

// 
function handleSubmit (form) {
  const { getFieldsValue } = form
  console.warn(getFieldsValue())
}

function SearchboxHorizontal (props) {
  const { form } = props

  return (
    <div>
      <SearchBox dataSource={dataSource} form={form} onSubmit={() => handleSubmit(form)} />
    </div>
  )
}

export default Form.create({ name: 'search_box_horizontal' })(SearchboxHorizontal)
