import React from 'react'
import { Form, InputNumber } from 'antd'
import WarnBox from '../../common/warn_box'

function SpaceChoice (props) {
  const { form } = props

  // formform
  if (!form) {
    return <WarnBox title='form' />
  }

  const { getFieldDecorator } = form

  return (
    <div className='htht-space-choice htht-space-choice-two-line'>
      <span className='title'></span>
      <div className='item'>
        <span className='sub-title'>
          <i></i>
        </span>
        <Form.Item>
          {getFieldDecorator('minLong', {
            rules: [{ required: true, message: '' }]
          })(
            <InputNumber placeholder='' min={-180} max={180} style={{ width: '1.4rem' }} />
          )}
        </Form.Item>
        <span className='gap'>-</span>
        <Form.Item>
          {getFieldDecorator('maxLong', {
            rules: [{ required: true, message: '' }]
          })(
            <InputNumber placeholder='' min={-180} max={180} style={{ width: '1.4rem' }} />
          )}
        </Form.Item>
      </div>

      <div className='item'>
        <span className='sub-title'>
          <i></i>
        </span>
        <Form.Item>
          {getFieldDecorator('minLat', {
            rules: [{ required: true, message: '' }]
          })(<InputNumber placeholder='' min={-90} max={90} style={{ width: '1.4rem' }} />)}
        </Form.Item>
        <span className='gap'>-</span>
        <Form.Item>
          {getFieldDecorator('maxLat', {
            rules: [{ required: true, message: '' }]
          })(<InputNumber placeholder='' min={-90} max={90} style={{ width: '1.4rem' }} />)}
        </Form.Item>
      </div>
    </div>
  )
}

export default SpaceChoice
