import React, { Fragment } from 'react'
import { Form, DatePicker } from 'antd'
import WarnBox from '../../common/warn_box'

function TimeChoice (props) {
  const { form, mode } = props
  // modehorizontalverticalhorizontal
  const checked = mode && mode === 'vertical'

  // formform
  if (!form) {
    return <WarnBox title='form' />
  }

  // class
  const className = checked ? 'htht-time-choice-vertical' : 'htht-time-choice-horizontal'
  const { getFieldDecorator } = form

  return (
    <div className={`htht-time-choice ${className}`}>
      <div className='item'>
        {checked ? (
          <span className='title'></span>
        ) : (
          <span className='title'></span>
        )}
        <Form.Item>
          {getFieldDecorator('startTime', {
            rules: [{ required: true, message: '' }]
          })(
            <DatePicker
              showToday={false}
              format='YYYY-MM-DD'
              allowClear={false}
              style={{ width: '1.4rem' }}
            />
          )}
        </Form.Item>
        {!checked ? (
          <Fragment>
            <span className='gap'>-</span>
            <Form.Item>
              {getFieldDecorator('endTime', {
                rules: [{ required: true, message: '' }]
              })(
                <DatePicker
                  showToday={false}
                  format='YYYY-MM-DD'
                  allowClear={false}
                  style={{ width: '1.4rem' }}
                />
              )}
            </Form.Item>
          </Fragment>
        ) : null}
      </div>
      {checked ? (
        <div className='item'>
          <span className='title'></span>
          <Form.Item>
            {getFieldDecorator('endTime', {
              rules: [{ required: true, message: '' }]
            })(
              <DatePicker
                showToday={false}
                format='YYYY-MM-DD'
                allowClear={false}
                style={{ width: '1.4rem' }}
              />
            )}
          </Form.Item>
        </div>
      ) : null}
    </div>
  )
}

export default TimeChoice
